@import url('https://fonts.googleapis.com/css2?family=Fira+Code:wght@400;500;600;700&display=swap');

#main {
    padding-top: 60px;
    position: relative;
    border-bottom: 2px solid rgb(141, 137, 137);
    padding-left: 20px;
    padding-right: 20px;
}

#part1 {
    text-align: left;
    font-family: Poppins;
    margin-left: 100px;
    font-size: 40px;
}

#hello {
    font-family: Poppins;
    background-color: rgb(247, 57, 57);
    margin-top: 100px;
    color: midnightblue;
}

#rest {
    font-family: Fira Code;
    font-size: 50px;
}

#part2 {
    position: absolute;
    top: 18%;
    left: 72%;
    border-radius: 10%;
    overflow: hidden;
    border: 5px solid midnightblue;
    display: flex;
    align-items: center;
    justify-content: center;
}

#part2 img {
    width: 280px; 
    height: 350px; 
    max-width: 500px; 
}

.button-79 {
    backface-visibility: hidden;
    background: rgb(40, 40, 168);
    border: 0;
    border-radius: .375rem;
    box-sizing: border-box;
    color: #fff;
    cursor: pointer;
    font-family: Poppins;
    font-size: 20px;
    font-weight: 700;
    letter-spacing: -.01em;
    line-height: 1.3;
    height: 60px;
    width: 140px;
    margin-top: 50px;
    margin-bottom: 50px;
}

.button-79:hover {
    scale: 1.1;
    background-color: midnightblue;
    transition: ease-in-out;
}

/* Responsive Design */
@media (max-width: 1024px) {
    #part1 {
        font-size: 35px;
        margin-left: 50px;
    }

    #rest {
        font-size: 40px;
    }

    #part2 {
        top: 20%;
        left: 65%;
        transform: translate(-50%, 0);
    }

    #part2 img {
        width: 250px; 
        height: 320px;
    }
}

@media (max-width: 768px) {
    #part1 {
        font-size: 30px;
        margin-left: 20px;
    }

    #rest {
        font-size: 35px;
    }

    #part2 {
        top: 25%;
        left: 60%;
        transform: translate(-50%, 0);
    }

    #part2 img {
        width: 220px; 
        height: 300px;
    }

    .button-79 {
        width: 120px;
        font-size: 18px;
        height: 50px;
    }
}

@media (max-width: 480px) {
    #main {
        padding-top: 40px;
        padding-left: 10px;
        padding-right: 10px;
    }

    #part1 {
        font-size: 25px;
        margin-left: 0;
        text-align: center;
    }

    #hello {
        font-size: 22px;
    }

    #rest {
        font-size: 28px;
        text-align: center;
    }

    #part2 {
        position: static;
        margin: 20px auto;
        transform: none;
        width: 80%;
    }

    #part2 img {
        width: 100%; 
        height: auto;
    }

    .button-79 {
        width: 100%;
        font-size: 16px;
        height: 45px;
    }
}
