@import url('https://fonts.googleapis.com/css2?family=Fira+Code:wght@400;500;600;700&display=swap');

.header {
    background-color: midnightblue;
    color: white;
    position: fixed;
    width: 100%;
    display: flex;
    justify-content: space-between;
    height: 60px;
    align-items: center;
    z-index: 1000;
    padding: 0 20px;
    box-sizing: border-box;
}

#logo {
    font-size: 20px;
    font-family: 'Fira Code', monospace;
}

.header nav ul {
    font-family: 'Merriweather', serif;
    list-style: none;
    display: flex;
    justify-content: space-around;
    gap: 40px;
    font-size: 18px;
    padding: 0;
    margin: 0;
}

.header nav ul li {
    margin: 0;
}

.header nav ul li a {
    color: #dadada;
    text-decoration: none;
    font-weight: bold;
}

.header nav ul li a:hover {
    color: white;
}


@media (max-width: 768px) {
    .header {
        padding: 0 15px; 
        height: 50px; 
    }

    #logo {
        font-size: 18px; 
    }

    .header nav ul {
        gap: 20px; 
        font-size: 16px; 
    }
}

@media (max-width: 600px) {
    .header {
        padding: 0 10px;
        height: 45px; 
    }

    #logo {
        font-size: 16px; 
    }

    .header nav ul {
        gap: 15px; 
        font-size: 14px; 
    }
}

@media (max-width: 480px) {
    .header {
        padding: 0 5px; 
        height: 40px; 
    }

    #logo {
        font-size: 14px; 
    }

    .header nav ul {
        gap: 10px; 
        font-size: 12px; 
    }
}
