#Project_main {
    margin-top: 60px;
    display: flex; 
    flex-direction: column; 
    justify-content: center; 
    align-items: center; 
    position: relative;
    width: 100%; 
}

#pro {
    font-family: 'Poppins', sans-serif;
    font-size: 60px;
    margin-bottom: 10px;
    text-align: center; 
}

#ject {
    margin-top: 0;
    color: midnightblue;
    opacity: 0.7;
    text-align: center; 
}

#Pdisplay {
    width: 80%;
    border: 3px solid midnightblue; 
    margin: 20px 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between; 
    gap: 20px;
    padding: 50px;
    align-items: flex-start;
    border-radius: 8px;
    background-color: rgb(173, 173, 255);
    box-shadow: 0 0 15px rgba(0, 0, 255, 0.5); 
    animation: glow 1.5s infinite alternate;
}

@keyframes glow {
    from {
        box-shadow: 0 0 10px rgba(0, 0, 255, 0.5);
    }
    to {
        box-shadow: 0 0 20px rgba(0, 0, 255, 1);
    }
}

.project-card {
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    width: calc(50% - 20px); 
    overflow: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    transition: transform 0.2s, box-shadow 0.2s;
    margin-bottom: 20px; 
    font-family: 'Fira Code';
}

.project-card:hover {
    transform: translateY(-10px);
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

.project-image {
    width: 100%;
    height: 180px;
    object-fit: cover; 
}

.project-title {
    font-size: 1.2em;
    margin: 10px 0;
    color: midnightblue;
}

.project-description {
    font-size: 1em;
    color: #555;
    padding: 0 10px;
}

.project-link {
    margin: 10px 0;
    padding: 10px 20px;
    background-color: blue;
    color: white;
    text-decoration: none;
    border-radius: 4px;
    transition: background-color 0.2s;
}

.project-link:hover {
    background-color: darkblue;
}


@media (max-width: 1024px) {
    #Pdisplay {
        padding: 30px; 
    }

    .project-card {
        width: calc(50% - 15px); 
    }
}

@media (max-width: 768px) {
    #Pdisplay {
        padding: 20px; 
    }

    .project-card {
        width: 100%; 
        margin-bottom: 20px;
    }
}

@media (max-width: 480px) {
    #pro {
        font-size: 40px; 
    }

    #Pdisplay {
        width: 90%; 
        padding: 10px; 
    }

    .project-card {
        width: 100%; 
        margin-bottom: 15px; 
    }

    .project-title {
        font-size: 1em; 
    }

    .project-description {
        font-size: 0.9em; 
    }
}
