.main {
    margin-top: 30px;
    position: relative;
    padding: 20px;
}

#about {
    font-family: 'Poppins', sans-serif;
    font-size: 60px;
    margin-bottom: 10px;
    text-align: center; 
}

#intro {
    margin-top: 0;
    color: midnightblue;
    top: 75%;
    left: 45%;
    opacity: 0.7;
    text-align: center; 
}

.content {
    display: flex;
    justify-content: space-around;
    align-items: center; 
    gap: 150px; 
    flex-wrap: wrap;
}

#gif video {
    width: 100%; 
    max-width: 500px;
    height: auto;
}

#para {
    margin-top: 70px;
    text-align: left;
    font-size: 18px;
    font-family: 'Fira Code', monospace;
    word-spacing: 5px;
    flex: 1; 
}

#icons {
    display: flex;
    gap: 20px;
    flex-wrap: wrap; 
    justify-content: center; 
}

.icon {
    margin-top: 30px;
    margin-left: 20px;
    height: 55px;
    width: 55px;
    transition: transform 0.3s ease-in-out; 
}

.icon:hover {
    transform: scale(1.2); 
    cursor: pointer;
}

#resumeBtn {
    display: flex;
    justify-content: center;
    margin-top: 20px;
}

#aboutBtn {
    width: 130px;
    height: 50px;
    font-size: 18px; 
    font-family: 'Fira Code';
    border-radius: 10px;
    background-color: rgb(40, 40, 168);
    cursor: pointer;
    color: white;
    transition: background-color 0.3s ease;
}

#aboutBtn:hover {
    background-color: rgb(30, 30, 150);
}


@media (max-width: 768px) {
    .content {
        flex-direction: column; /* Stack content vertically */
        align-items: center; /* Center-align for smaller screens */
        gap: 20px; /* Reduce gap for smaller screens */
    }

    #para {
        text-align: center; /* Center text on smaller screens */
        margin-top: 20px;
    }

    #gif video {
        max-width: 100%; /* Full width for smaller screens */
        height: auto;
    }

    .icon {
        margin-top: 20px;
        margin-left: 0; /* Remove left margin for smaller screens */
    }

    #aboutBtn {
        font-size: 16px; /* Smaller font size for better fit */
        margin-left: 0; /* Center-align button */
    }
}

@media (max-width: 480px) {
    #about {
        font-size: 40px; /* Smaller heading font size */
    }

    #intro {
        font-size: 18px; /* Adjust subheading font size */
    }

    #para {
        font-size: 16px; /* Smaller text for better readability */
    }

    #aboutBtn {
        width: 100px; /* Smaller button width */
        height: 40px; /* Smaller button height */
        font-size: 14px; /* Smaller button font size */
    }

    .icon {
        height: 40px; /* Smaller icon size */
        width: 40px; /* Smaller icon size */
    }
}
